import React, { useState, useRef, MutableRefObject, useEffect } from "react"
import styled from "styled-components"
import { TextButton } from "../../styles/custom-styled-components"
import { Themed } from "../../styles/theme"

const _Form = styled.form`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`

const _Input = styled.input`
  border: none;
  outline: none;
  background-color: none;
  border-bottom: 2px solid ${({ theme }: Themed) => theme.skin.contrastText};
  margin-bottom: 20px;
  padding: 7px;
  max-width: 90%;

  &:focus {
    outline: none;
  }

  &::placeholder {
    transition: color 0.3s;
    color: transparent;
  }
  &.animate {
    &::placeholder {
      color: ${({ theme }: Themed) => theme.skin.contrastText};
    }
  }
`

const _Label = styled.label`
  font-size: 16px;
  line-height: 10px;
  transform: translate3d(10px, 25px, 0);
  transition: transform 0.3s, opacity 0.3s;
  &.animate {
    transform: translate3d(2px, 60px, 0) scale(0.85);
  }
`

const _NameInput = styled(_Input)`
  width: 13rem;
`

const _EmailInput = styled(_Input)`
  width: 21rem;
`

const _MessageInput = styled(_Input)`
  width: 34rem;
`

const ContactForm = () => {
  const [nameInputClass, setNameInputClass] = useState("")
  const [emailInputClass, setEmailInputClass] = useState("")
  const [tmessageAreaInputClass, setMessageAreaInputClass] = useState("")

  const inputName = useRef({} as HTMLInputElement)
  const inputEmail = useRef({} as HTMLInputElement)
  const inputMessage = useRef({} as HTMLInputElement)

  function checkIfShouldAnimate(
    setInputClass: React.Dispatch<React.SetStateAction<string>>,
    someInput: MutableRefObject<HTMLInputElement>
  ) {
    if (!someInput.current.value.trim()) {
      setInputClass("")
    } else {
      setInputClass("animate")
    }
  }

  useEffect(() => {
    checkIfShouldAnimate(setNameInputClass, inputName)
    checkIfShouldAnimate(setEmailInputClass, inputEmail)
    checkIfShouldAnimate(setMessageAreaInputClass, inputMessage)
  }, [])

  return (
    <_Form
      action="https://formspree.io/edgardo.arriagada.ortiz@gmail.com"
      method="post"
    >
      <_Label htmlFor="name" className={nameInputClass}>
        Name
      </_Label>
      <_NameInput
        className={nameInputClass}
        id="name"
        type="text"
        name="name"
        placeholder="Your name"
        onFocus={_ => setNameInputClass("animate")}
        onBlur={_ => checkIfShouldAnimate(setNameInputClass, inputName)}
        ref={inputName}
      />

      <_Label htmlFor="email" className={emailInputClass}>
        Email
      </_Label>
      <_EmailInput
        className={emailInputClass}
        id="email"
        type="email"
        name="email"
        placeholder="Your email"
        onFocus={_ => setEmailInputClass("animate")}
        onBlur={_ => checkIfShouldAnimate(setEmailInputClass, inputEmail)}
        ref={inputEmail}
      />

      <_Label htmlFor="message" className={tmessageAreaInputClass}>
        Message
      </_Label>
      <_MessageInput
        className={tmessageAreaInputClass}
        id="message"
        name="message"
        placeholder="Your message"
        onFocus={_ => setMessageAreaInputClass("animate")}
        onBlur={_ =>
          checkIfShouldAnimate(setMessageAreaInputClass, inputMessage)
        }
        ref={inputMessage}
      />

      <TextButton type="submit" primary style={{ margin: "0.8rem 0" }}>
        <strong>Send</strong>
      </TextButton>
    </_Form>
  )
}

export default ContactForm
