import React, { useEffect } from "react"
import styled from "styled-components"
import { Themed } from "../../styles/theme"
import { useAppStore } from "../../Store/Store"
import Logo from "../../assets/logo.svg"
import Navigation from "./navigation"
import SocialMedia from "./social-media"

const s = require("./drawer.module.scss")

const _Drawer = styled.div`
  position: fixed;
  display: flex;
  align-items: flex-start;
  height: 120%; /*120% fix weird scroll issues on mobile and tablets*/
  width: ${({ theme }: Themed) => theme.drawer.width};
  background-color: ${({ theme }: Themed) => theme.skin.main};
  color: ${({ theme }: Themed) => theme.skin.contrastText};
  box-shadow: ${({ theme }: Themed) => theme.dp.dp4};
  top: 0;
  left: 0;
  max-width: 70%;
  z-index: 200;
  transition: transform 0.3s ease-out;
  @media (min-width: 760px) {
    transform: translate3d(0, 0, 0);
  }
`

// this is to avoid the bad-supported space-evenly on _Drawer
const _DrawerContentWrapper = styled.div`
  padding: 4rem 0 3rem;
  height: 83%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
`

const _Logo = styled(Logo)`
  width: 4rem;
  height: 4rem;
`

const Drawer = () => {
  const { UIState, UIDispatch } = useAppStore()

  function closeDrawer() {
    UIDispatch({ type: "UI_CLOSE_DRAWER" })
  }

  useEffect(() => {
    window.addEventListener("resize", closeDrawer)
    return () => {
      window.removeEventListener("resize", closeDrawer)
    }
  }, [])

  return (
    <_Drawer className={UIState.isDrawerOpen ? s.opened : s.closed}>
      <_DrawerContentWrapper>
        <_Logo />
        <Navigation />
        <SocialMedia />
      </_DrawerContentWrapper>
    </_Drawer>
  )
}

export default Drawer
