import React from "react"
import styled from "styled-components"
import { Themed } from "../../styles/theme"
import { useAppStore } from "../../Store/Store"
import Scrollspy from "react-scrollspy"

const _Li = styled.li`
  a {
    position: relative;
    overflow: hidden;

    padding: 0.4rem;
    color: ${({ theme }: Themed) => theme.skin.contrastText};
    text-decoration: none;
    &:before {
      content: "";
      position: absolute;
      width: 250%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: -1;
      background-image: ${({ theme }: Themed) => theme.getLinearGradient()};
      transform: translate3d(-250%, 0, 0);
      transition: transform 0.5s ease-in-out;
    }
  }
`

const _Nav = styled.nav`
  height: 6rem; /* This should be changed to change spacing between anchors */
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;

  .link-active {
    a {
      transition: color 1s ease-out;
      color: black;
      &:before {
        transform: translate3d(-80%, 0, 0);
      }
    }
  }
`

const Navigation = () => {
  const { UIDispatch } = useAppStore()

  function handleClick() {
    UIDispatch({ type: "UI_CLOSE_DRAWER" })
  }

  return (
    <_Nav>
      <Scrollspy
        items={["home", "about", "portfolio", "contact"]}
        currentClassName="link-active"
        style={{
          height: "100%",
          display: "flex",
          flexDirection: "column",
          justifyContent: "space-between",
          listStyleType: "none",
          margin: "0",
        }}
      >
        <_Li>
          <a href="#home" onClick={handleClick}>
            Home
          </a>
        </_Li>
        <_Li>
          <a href="#about" onClick={handleClick}>
            About
          </a>
        </_Li>
        <_Li>
          <a href="#portfolio" onClick={handleClick}>
            Portfolio
          </a>
        </_Li>
        <_Li>
          <a href="#contact" onClick={handleClick}>
            Contact
          </a>
        </_Li>
      </Scrollspy>
    </_Nav>
  )
}

export default Navigation
