import React from "react"
import { MdSort } from "react-icons/md"
import styled from "styled-components"
import { useAppStore } from "../Store/Store";

const _BurgerButton = styled(MdSort)`
  position: fixed;
  z-index: 200;
  color: white;
  border-radius: 3rem;
  padding: 0.1rem;
  top: 0.7rem;
  left: 0.7rem;
  background-color: rgba(0, 0, 0, 0.5);
  font-size: 2.5rem;
`

const BurgerButton = () => {
  const {UIDispatch} = useAppStore()
  
  function clickHandler() {
    UIDispatch({type: 'UI_OPEN_DRAWER'})
  }

  return <_BurgerButton onClick={clickHandler} />
}

export default BurgerButton
