/// <reference path="../custom-typings/tsd.d.ts" />
import React from "react"
import LandingPage from "./landing-page"
import Footer from "./footer"
import Backdrop from "./backdrop"
import Head from "./head"
import BurgerButton from "./burger-button"

import styled from "styled-components"
import Drawer from "./drawer/drawer"
import { Themed } from "../styles/theme"

const _Container = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
`

const _PageContent = styled.div`
  margin: 1rem auto 4rem auto;
  width: 90%;
  @media (min-width: 760px) {
    width: 83%;
  }
`

const _MainContent = styled.div`
  width: 100%;
  @media (min-width: 760px) {
    margin-left: ${({ theme }: Themed) => theme.drawer.width};
    width: ${({ theme }: Themed) => theme.drawer.getRest()};
  }
`
/*Initialize SmoothScroll*/

if (typeof window !== "undefined") {
  require("smooth-scroll")('a[href*="#"]')
}

const Layout = ({ children, title }: Props) => {
  return (
    <_Container>
      <BurgerButton />
      <Backdrop />
      <Drawer />
      <_MainContent>
        <Head title={title} />
        <LandingPage />
        <_PageContent>{children}</_PageContent>
        <Footer />
      </_MainContent>
    </_Container>
  )
}

type Props = {
  children: JSX.Element
  title?: string
}

export default Layout
