import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import styled from "styled-components"
import { Themed } from "../styles/theme"
import { FaGithub, FaExternalLinkAlt } from "react-icons/fa"
import { IconButton } from "../styles/custom-styled-components"

const _List = styled.ul`
  margin: 0;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minMax(300px, 1fr));
  grid-gap: 1rem;
  list-style: none;
  align-items: stretch;
`

const _ListItem = styled.li`
  color: ${({ theme }: Themed) => theme.skin.dark};
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: ${({ theme }: Themed) => theme.dp.dp1};
`

const _ImageContainer = styled.div`
  width: 100%;
  height: auto;
`

const _LabelsContainer = styled.div`
  padding: 0.7rem;
  p {
    color: ${({ theme }: Themed) => theme.skin.contrastText};
    text-align: center;
    font-style: italic;
  }
`

const _ActionsContainer = styled.div`
  padding: 0.5rem;
  width: 100%;
  display: flex;
  justify-content: flex-end;
`

const Portfolio = () => {
  const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark {
        edges {
          node {
            frontmatter {
              name
              shortDescription
              liveLink
              githubLink
            }
            html
          }
        }
      }
    }
  `)

  if (data.error) {
    throw new Error(data.error)
  }

  const { edges } = data.allMarkdownRemark

  const Posts = edges.map((edge: any) => {
    const { node } = edge
    const { name, shortDescription, githubLink, liveLink } = node.frontmatter
    const { html } = node
    return (
      <_ListItem key={name}>
        <_LabelsContainer>
          <h3>{name}</h3>
        </_LabelsContainer>
        <_ImageContainer dangerouslySetInnerHTML={{ __html: html }} />
        <_LabelsContainer>
          <p>{shortDescription}</p>
        </_LabelsContainer>
        <_ActionsContainer>
          {githubLink ? (
            <IconButton href={githubLink} target="_blank">
              <FaGithub />
            </IconButton>
          ) : (
            <IconButton disabled>
              <FaGithub />
            </IconButton>
          )}
          {liveLink ? (
            <IconButton primary href={liveLink} target="_blank">
              <FaExternalLinkAlt />
            </IconButton>
          ) : (
            <IconButton disabled>
              <FaExternalLinkAlt />
            </IconButton>
          )}
        </_ActionsContainer>
      </_ListItem>
    )
  })

  return <_List>{Posts}</_List>
}

export default Portfolio
