import React, { useEffect } from "react"
import styled from "styled-components"
import { useAppStore } from "../Store/Store"

const s = require("./backdrop.module.scss")

const _Backdrop = styled.div`
  position: fixed;
  height: 120%; /*120% fix weird scroll issues on mobile and tablets*/
  width: 100%;
  z-index: 100;
  background-color: rgb(0, 0, 0, 0.5);
  @media (min-width: 760px) {
    display: none;
  }
`

const Backdrop = () => {
  const { UIState, UIDispatch } = useAppStore()
  function handleClick() {
    UIDispatch({ type: "UI_CLOSE_DRAWER" })
  }

  useEffect(() => {
    if (UIState.isDrawerOpen) {
      document.documentElement.style.overflowY = "hidden"
    } else {
      document.documentElement.style.overflowY = "scroll"
    }
  })

  return (
    <_Backdrop
      className={UIState.isDrawerOpen ? s.opened : s.closed}
      onClick={handleClick}
    />
  )
}

export default Backdrop
